import React from 'react';
import PropTypes from 'prop-types';
import './skl-ds-card.scss';

import { Text } from './../../atoms/skl-ds-text/skl-ds-text';

export const Card = ({ color,text, fontSize, colorText }) => {
  const styles = {
    backgroundColor: color,
  };

  return <div className="skl-ds-card" style={styles}>
      <Text tag={'p'} children={text} fontSize={'small'} color={colorText}/>
  </div>
};

Card.propTypes = {
  color: PropTypes.string,
};

Card.defaultProps = {
  color: '#fff',
};
